button {
    background-color: white;
    opacity: 90%;
    border: none;
    border-radius: 1px;
}

button:hover {
    cursor: pointer;
    font-weight: bold;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    button {
        width: 30px;
    } 
}

